.tools {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  padding-bottom: 80px;
}

.section {
  background-color: #f5f2ee;
  padding-top: 90px;
  padding-bottom: 20px;
  .h2 {
    text-align: center;
  }
  .text {
    font-size: rem(16);
    color: #8c8c8c;
    line-height: 28px;
    max-width: 720px;
    text-align: center;
    margin: 0 auto;
    font-weight: $light;
    padding-top: 14px;
  }
  .center-box {
    display: flex;
    justify-content: space-between;
    padding-top: 82px;
    position: relative;
    margin-top: 32px;
    img {
      width: 50%;
      &.img-layer {
        position: absolute;
        top: 0;
        max-width: 770px;
        left: 50%;
        margin-left: -385px;
        width: 100%;
      }
    }
  }
  .bottom-box {
    background-color: #3E2D27;
    padding-top: 50px;
    padding-bottom: 50px;
    .bottom-box-inner {
      display: flex;
      justify-content: space-between;
      padding: 0 45px;
      .left {
        .link {
          font-size: rem(28);
          color: $w;
          font-family: $font-title;
          span {
            color: #c7b299;
          }
        }
        .bot-text {
          font-size: rem(18);
          color: #615855;
          display: block;
          padding-top: 9px;
        }
      }
    }
  }
}

.tabs-section {
  padding-top: 32px;
  .tabs-group {
    display: flex;
    justify-content: center;
    padding-bottom: 40px;
    .tabs-item {
      &:not(:nth-last-child(1)) {
        margin-right: 10px;
      }
      &.active {
        a {
          color: $w;
          background-color: #998575;
        }
      }
      a {
        border: 1px solid #dcd8d8;
        border-radius: 3px;
        height: 32px;
        line-height: 29px;
        font-weight: $light;
        display: inline-block;
        color: $col;
        padding: 0 21px;
        &:hover {
          color: $w;
          background-color: #998575;
        }
      }
    }
  }
  .tabs-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.devise {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  .devise-box {
    width: 660px;
    margin-top: 110px;
    padding-right: 10px;
    .h3 {
      margin-top: 0;
    }
    p {
      line-height: 28px;
      font-size: rem(16);
    }
    .devise-list {
      padding-left: 50px;
      padding-top: 11px;
      .devise-list-item {
        margin-bottom: 9px;
        line-height: 23px;
        a {
          color: $col;
          font-size: rem(16);
          display: block;
          position: relative;
          padding-left: 49px;
          &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 50%;
            margin-top: -10px;
            @include sprite($arr-list);
          }
        }
      }
    }
  }
  .img-box {
    padding-left: 50px;
    display: block;
  }
}

.counters {
  background: linear-gradient(90deg, #211b19 0%, #4e3326 100%);
  padding-top: 35px;
  padding-bottom: 62px;
  .counter-box {
    display: flex;
    justify-content: space-between;
    &-item {
      position: relative;
      width: 25%;
      text-align: center;
      padding-top: 60px;
      padding-bottom: 25px;
      margin-top: 30px;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        margin-left: -25px;
      }
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        height: 2px;
        background-color: #fff;
        width: 35px;
        left: 50%;
        margin-left: -17.5px;

      }
      &.cl {
        &:before {
          @include sprite($coun-1);
        }
      }
      &.cup {
        &:before {
          @include sprite($coun-2);
        }
      }
      &.bl {
        &:before {
          @include sprite($coun-3);
        }
      }
      &.hr {
        &:before {
          @include sprite($coun-4);
        }
      }
      &.lch {
        &:before {
          @include sprite($coun-5);
        }
      }
      .counter {
        display: block;
        font-size: rem(48);
        color: $w;
        font-weight: $light;
        margin-bottom: 5px;
      }
      .counter-name {
        display: block;
        color: $cr;
        text-transform: uppercase;

      }
    }
  }
}

.slider-posts {
  padding-bottom: 50px;
  .slider-box-posts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    overflow: hidden;
    .slick-list {
      margin-right: -20px;
    }
    .slick-slide {
      margin-right: 20px;
    }
    .slider-post-item {

    }
  }
  .buttons-arr-slick {
    position: relative;
    text-align: center;
    .slick-arrow {
      @include sprite($arr-r);
      &.slick-prev{
        transform: rotate(190deg);
        margin-right: 10px;
      }
    }
  }
}

.partners{
  min-height: 480px;
  overflow: hidden;
  background-image: url('img/tmp/bg-part.jpg');
  position: relative;
  display: flex;
  align-items: center;
  &:before{
    content: "";
    position: absolute;
    top:0;
    bottom: 0;
    right: 0;
    left:0;
    background-color: rgba(0,0,0, 0.5);
  }
  .partners-tems{
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    z-index: 10;
    flex-basis: 100%;
  }
}






